import Vue from 'vue'
import Router from 'vue-router'


const Home = () => import('@/pages/home/index.vue');
const Favorite = () => import('@/pages/favorite/index.vue');
const Recommended = () => import('@/pages/recommended/index.vue');
const Rankpage = () => import('@/pages/rankpage/index.vue');
const Search = () => import('@/pages/search/index.vue');
Vue.use(Router);

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      meta:{title:'首页'},
      component: Home
    },
    {
      path: '/favorite',
      name: 'Favorite',
      meta:{title:'收藏夹'},
      component: Favorite
    },
    {
      path: '/recommended',
      name: 'Recommended',
      meta:{title:'精品推荐'},
      component: Recommended
    },
    {
      path: '/rankpage',
      name: 'Rankpage',
      meta:{title:'排行榜'},
      component: Rankpage
    },
    {
      path: '/search',
      name: 'Search',
      meta:{title:'搜索'},
      component: Search
    }
  ]
})



